import React from 'react';
import "./Estilos.css";

export default function Estilos() {
    let misEstilos = {
        borderRadius: ".50rem",
        margin: "1.5rem auto",
        maxWidth: "50%",
        //No recomendado, problemas con guiones medios, espacios y ma
        //cosas que no se pueden hacer en un archivo js

    }
    return (
        <section className="estilos">
            <h2>Estilos CSS con React</h2>
            <h3 className="bg-react">Estilos de hoja css</h3>
            <h3 className="bg-react" style={{borderRadius:".25rem", margin:"1rem"}}>Estilos en linea</h3>
            <h3 className="bg-react" style={misEstilos}>Estilos en variables internas</h3>
            <p>Existen otras técnicas, como reglas de normalize, modulos y utilizando SASS ver video curso react 23</p>
        </section>
    );
}