import React, { useState } from 'react';

export default function Formularios() {
    const [form, setForm] = useState({});

    //Cada vez que llamen la funcion, agrega el valor de la propiedad
    //de quien lo llamo, y añadelo como valor a el objeto form
    const handleChange = (e) => {
        setForm({
            ...form, [e.target.name]: e.target.value,
        });
    }

    //Cada vez que llamen la funcion, agrega el valor de la propiedad
    //de quien lo llamo, y añadelo como valor booleano a el objeto form
    const handleChecked = (e) => {
        setForm({
            ...form, [e.target.name]: e.target.checked,
        });
    }


    const handleSubmit = (e) => {
        e.preventDefault();

        alert("formulario enviado");
    }

    return (
        <>
            <h2>Formularios</h2>
            
            <form onSubmit={handleSubmit}>
                <p>Forma de controlar elementos Inputs Text con estados</p>
                
                <label htmlFor="nombre">Nombre:  </label>
                <input type="text" id="nombre" name="nombre" value={form.nombre} onChange={handleChange} />
                
                <p>Forma de controlar elementos Inputs Radio con estados</p>
                <h3>Sabor JS Preferido</h3>
                
                {/* Si queremos un valor por defecto es con defaultChecked */}
                <input type="radio" id="vanilla" name="sabor" value="vanilla" onChange={handleChange} defaultChecked/>
                <label htmlFor="vanilla">Vanilla</label>

                <input type="radio" id="React" name="sabor" value="React" onChange={handleChange}/>
                <label htmlFor="React">React</label>

                <input type="radio" id="Vue" name="sabor" value="Vue" onChange={handleChange}/>
                <label htmlFor="Vue">Vue</label>

                <input type="radio" id="Angular" name="sabor" value="Angular" onChange={handleChange}/>
                <label htmlFor="Angular">Angular</label>
            
                <br />
                <br />
                
                
                <p>Lenguaje de Programación Preferido</p>

                <select name="lenguaje" onChange={handleChange}>
                    <option value="">---</option>
                    <option value="js">JavaScript</option>
                    <option value="php">PHP</option>
                    <option value="py">Python</option>
                    
                </select>


                <br />
                <br />

                <p>Check</p>
                <label htmlFor="terminos">Acepto términos y condiciones</label>
                <input type="checkbox" id="terminos" name="terminos" onChange={handleChecked}></input>
            
                <br />
                <br />
                <input type="submit"/>
            </form>

            
        </>
        
    )
}