import React, { Component } from 'react';

/* Componentes que indica si el usuario esta registrado o NO */
function Login() {
    return (
        <div>
            <h4> Usuario Registrado</h4>
        </div>
    );
}

function Logout() {
    return (
        <div>
            
            <h4>Usuario No Registrado</h4>
        </div>
    );
}


//Para saber que componeten mostrar se crea una variable de estado
//con su contructor.
export default class RenderizadoCondicional extends Component{
    constructor(props) {
        super(props);
        this.state = {
            //Si session es true muestra login(), si es false muestra logout
            session:true,
        }
    }
    
    render() {
        return (
            <div>
                <h2>Renderizado Condicional</h2>
                {/* Si session es true renderiza login(), si es false muestra logout */}
                {this.state.session ? <Login /> : <Logout />}
            </div>
        );
    }
}