import React, { Component } from 'react';
import Data from "../helpers/data.json";

function ListElemet(props) {

    return (
        <li><a href={props.el.web} target="_blank" rel="noreferrer">{props.el.name}</a></li>
    )
}


export default class RenderizadoElements extends Component{
    constructor(props) {
        super(props);
        this.state = {
            seasons:["primavera", "verano", "otoño","invierno"],
        }
    }

    /*  
    Si del estado queremos renderizar los elementos, se hace de la sig. manera 
    accedemos a la propiedad del estado "this.state.seasons" previamente
    ya definido con el constructor, en este caso "seasons" y en ese array
    por cada elemento vas a renderizar lo siguiente y agregamos el elemento
    que vamos a renderizar en JSX  this.state.seasons.map((el) => (<li>{el}</li>))
    
    */
    render() {
        return (
            <div>
                <h2>Renderizado de Elementos de clase</h2>
                <h4>Estaciones del año</h4>
                <ol>
                    {
                        this.state.seasons.map((el, index) => (
                            <li Key ={el.id}>{el}</li>
                        ))
                    }
                </ol>

                <hr/>
                <h3>Frameworks Fronts</h3>
                <p>Estos compontents se renderizan desde un archivo "json" sumulando una API Falsa</p>
                <ul>
                    {
                        Data.frameworks.map((el, index) => (
                            <ListElemet Key= {index} el={el}></ListElemet>
                        ))
                    }
                </ul>
            </div>
        );
    }
}