import React, { Component } from 'react';

//Boton componente usado en class EventosPersonalizados
function Boton(props) {
    return(<button onClick={props.myOnclick}>Boton Ya renderizado</button>)
}

//Forma de declarar eventos con ES6
export class EventosES6 extends Component{
    constructor(props) {
        super(props);
        this.state={
            contador: 0,
        }

        //Enlazamos el elemento this a la funcion sumar.
        this.sumar = this.sumar.bind(this);
        this.restar = this.restar.bind(this);
    }

    //Metodo de sumar
    sumar() {
        this.setState({
            contador: this.state.contador + 1,
        });
        
    }

    //Metodo de restar
    restar() {
        this.setState({
            contador: this.state.contador - 1,
        });
    }




    render() {
        return (
            <div>
                <h2>Events in Class Component ES6</h2>
                <h4>{this.state.contador}</h4>
                <nav>
                    <button onClick={this.sumar}>+1</button>
                    <button onClick={this.restar}>-1</button>
                    <br/>
                    <br/>
                </nav>
            </div>
        )
    }
}

//Forma de declarar eventos con ES7
export class EventosES7 extends Component{
    //Properties Initializer
    //gracias a estas se asigna el estado sin llamar al contructor

    state={
        contador: 0,
    }


    //Las arrow funtion heredan el this del contexto en que estan
    //por esto no se enlaza en this.
    sumar=(e)=> {
        this.setState({
            contador: this.state.contador + 1,
        });
    }

    //Metodo de restar
    restar=(e)=> {
        this.setState({
            contador: this.state.contador - 1,
        });
    }




    render() {
        return (
            <div>
                <h2>Events in Class Component ES7</h2>
                <h4>{this.state.contador}</h4>
                <nav>
                    <button onClick={this.sumar}>+1</button>
                    <button onClick={this.restar}>-1</button>
                    <br/>
                    <br/>
                </nav>
            </div>
        )
    }

}

//Pasar paremetros al manejador del evento
export class MasEventos extends Component{

    //Manejador del evento
    handleClick = (e, mensaje) => {
        console.log(e);
        console.log(mensaje);
    }

    render(){
        return (
            <div>
                <h2>Mas Sobre Eventos</h2>
                <p>Pasar parametros al manejador de eventos</p>
                <button onClick={(e)=> this.handleClick(e,"Pasando mensaje en handleClick")}>Saludar</button>
            </div>
        )
    }
}

//Pasar eventos personalizados
export class EventosPersonalizados extends Component{

    //Manejador del evento
    handleClick2 = (e, mensaje) => {
        console.log(e);
        console.log(mensaje);
    }

    render(){
        return (
            <div>
                <h2>Eventos Personalizados</h2>
                <p>Asignar eventos a componentes personalizados</p>
                <p>Al compontente le asignamos un evento personalizado, en este caso "myOnclick" y luego el evento. Le pasamos el evento como prop y en donde esta mi componente, destructuramos las props y ya hay asignamos el evento </p>
                <br />
                <Boton myOnclick={(e) => this.handleClick2(e, "Mensaje de Evento Personalizados")}>
                    Click</Boton>
                <br />
                <br />
                <br />
            </div>
        )
    }
}