






/* Podemos traer cualquier elemento de boostrap por ejemplo esta navBar
    solo necesitas una elemento funcional y cambiar lo sig. en el codigo:
    *Cambiar class por className
    *Si trae input, en html es: <input> en Jsx es : <input/>, (agregar "/" en input)
    *Existe un hr, pasa lo mismo, cerrar con "/>" al final del hr
    */

    function Navbar() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">
    <a className="navbar-brand" href="index.html">Navbar</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="index.html">Home</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="index.html">Link</a>
        </li>
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="index.html" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Dropdown
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><a className="dropdown-item" href="index.html">Action</a></li>
            <li><a className="dropdown-item" href="index.html">Another action</a></li>
            <li><hr className="dropdown-divider"/></li>
            <li><a className="dropdown-item" href="index.html">Something else here</a></li>
          </ul>
        </li>
        <li className="nav-item">
          <a className="nav-link disabled" href="index.html" tabIndex="-1" aria-disabled="true">Disabled</a>
        </li>
      </ul>
      <form className="d-flex">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit">Search</button>
      </form>
    </div>
  </div>
</nav>
        );
    }


export default function Boostrap() {


    return (
        <>
            <h2>BOOSTRAP</h2>
            {/* `Para que funcione, necesitas el cdn de boostrap en el index.html */}

            <Navbar></Navbar>
        </>

    );
}