import React, { Component } from 'react';

export default class Padre extends Component{

    state = {
        contador:0,
    }


    IncrementarContador=(e)=> {
        this.setState({
            contador: this.state.contador + 1,
        })
    }

    render(){
        return(
            <>
                <h2>Comunicación entre componentes</h2>
                <p>{"Contenedor Padre: "}{this.state.contador}</p>
                
                <Hijo IncrementarContador={this.IncrementarContador
                } mensaje="Hola soy Hijo 1"></Hijo>
                <Hijo IncrementarContador={this.IncrementarContador
                } mensaje="Hola soy Hijo 2"></Hijo>
                <br/>
                <br />
                <br />
                <p>El componente padre afecta el texto a los hijos</p>
                <p>EL boton que es hijo esta afectando el state del componente padre</p>
            </>
        )
    }
}


function Hijo(props) {
    return (
        <>
            <h4>{props.mensaje}</h4>
            <button onClick={props.IncrementarContador}>+1</button>
        </>
    )
}