import React from 'react';
import { useFetch } from "../Hooks/useFetch";

export default function HooksPersonalizados() {
    let url = "https://pokeapi.co/api/v2/pokemon?limit=3&offset=100";

    //Destructuramos e invocamos el hook
    let { data, pendiente, error } = useFetch(url);
    //

    console.log(data);
    return(
        <>
            <h2>Hooks Personalizados</h2>
            <h4>{JSON.stringify(pendiente)}</h4>
            <h4>
                <pre style={{whiteSpace:"pre-wrap"}}>
                    <code>
                        La data esta en el console.log
                        
                    </code>
                </pre>
            </h4>
            
            <h4>{JSON.stringify(error)}</h4>
            
        </>);
}