export default function Propiedades(props) {
    return (
        <div>
            <h2>{props.porDefecto}</h2>
            <p>Propiedades de este componente</p>
            <ul>
                <li>{props.cadena}</li>
                <li>{props.numero}</li>
                <li>{props.bol ? "Verdadero" : "falso"}</li>

                {/* AL ser un arreglo lo podemos manipular con sus metodos */}
                <li>{props.arreglo.join(", ")}</li>
                
                {/* Destructuramos el objeto */}
                <li>{`${props.objeto.nombre} - ${props.objeto.correo}`}</li>
                
                {/* Elementos de React */}
                <li>{ props.elementReact}</li>

                {/* Funciones en Propos */}
                <li>{props.arreglo.map(props.function).join(", ")}</li>
                

                {/* Pasar componentes como propiedades */}
                <li>{ props.componenteReact}</li>
            </ul>
        </div>
    );
}

/* Asignar valores por defecto a componente */
Propiedades.defaultProps = {
    porDefecto:"Titulo por Defecto",
};