import React, { Component } from 'react';

/* IMPORTANTE 
    descomentar los console.log ya que son importantes
    
*/


class Reloj extends Component{
    

    //Fase de desmontado
    componentWillUnmount() {
        //console.log(3, "Componente Eliminado del DOM");
    }


    render() {
        return <p>{this.props.hora}</p>
    }
}

export default class CicloVida extends Component{
    constructor(props) {
        super(props);
        //console.log(0, "Constructor: Component en fase 1, aun no esta el DOM");
    
        this.state = {
            hora: new Date().toLocaleTimeString(),
            visible: false,
        }

        //Variable con el clear intervar
        this.temporizador = null;
    }

    //DidMount es donde se hace la peticiones AJAX
    componentDidMount(){
    //console.log(1,"DidMount: Componente ya esta en el DOM")
    }

    //Permite obtener las props o estado de un Component antes de actualizarlas
    componentDidUpdate(prevProps, prevState) {
        //console.log(2, "Las props o estado del component han cambiado ");
        
        //Recibo 2 objetos, el primero las probs previas
        //console.log(prevProps);

        //el segundo el estado previo
        //console.log(prevState);
        console.log();
    }

    



    tiempo = () => {
        this.temporizador = setInterval(() => {
            this.setState({
                hora: new Date().toLocaleTimeString(),
            });
        }, 1000);
    };


    iniciar = () => {
        this.tiempo();
        this.setState({
            visible: true,
        });
    };

    detener = () => {
        clearInterval(this.temporizador);
        this.setState({
            visible: false,
        });
    };


    
    
    render() {
        //console.log(4,"Render: Ultima fase, Component Dibujado o Redibujado en el DOM")
        return (
            <>
                <h2>Ciclo de vida de los componentes de clase</h2>
                <h3>Temporizador</h3>
                {this.state.visible && <Reloj hora={this.state.hora}/>}
                
                <button onClick={this.iniciar}>Iniciar</button>
                <button onClick={this.detener}>Detener</button>
            </>
        )
    }
}