import React, { useState, useEffect } from 'react';

export default function SchollHooks() {
    const [scrollY, setscrollY] = useState(0);

    useEffect(() => {
        //console.log(1, "Fase de montaje");

        const DetectarScroll = () => {
            setscrollY(window.pageYOffset);
        }

        
        //Asignamos la funcion para que se ejecute cada
        //vez que reciba el evento scroll
        window.addEventListener("scroll", DetectarScroll);


        //Cuando el elemento ya no exista nos desubcribimos del evento
        return () => {
            window.removeEventListener("scroll", DetectarScroll);
            //console.log("Fase de desmontaje");
        }

    }, [scrollY]);
    //Pasarle el array, es decirle que solo se va a renderizar
    //cuando ese parametro tenga cambios.

    useEffect(() => {
        //console.log("Fase de desmontaje");

    });

    return (
        <>
            <h2>Hooks - useEffect y el ciclo de vida</h2>
            <p>Scroll esta a {scrollY } px</p>
        </>
    )
}