import { useState, useEffect } from 'react';

export const useFetch = (url) => {
    const [data, setdata] = useState(null);
    const [pendiente, setpendiente] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const getData = async (url) => {

            try {
                let res = await fetch(url);

                if (!res.ok) {
                    //Si existe el error crea este objeto error
                    throw {
                        err: true,
                        status: res.status,
                        statusText: !res.statusText ? "Ocurrio un error" : res.statusText,
                    };
                }


                //Si res.ok es true continua con la petición
                let json = await res.json();

                //Actualizamos el estado de las variables
                setpendiente(false);
                setdata(json);
                setError({ err: false });

            } catch (err) {
                setpendiente(true);
                setError(err);
            }

            
                
            
            /* json.results.forEach(async (el) => {//segunda peticion para obtener imagen
                let res = await fetch(el.url),
                    json = await res.json();
                    
                let pokemon = {
                    id: json.id,
                    name: json.name,
                    avatar: json.sprites.front_default,
                }

                setpokemons((pokemons) => [...pokemons, pokemon]);

            }); */
        
            
        }

        getData(url);
        
    }, [url]);

    return {data, pendiente, error};
}
