import React from 'react';

/* Componente Hijo que heredara el estado del padre */
function EstadoAHijo(props) {
    return (
        <div>
            <h5>Soy un componente hijo que hereda el estado del padre</h5>
            <p>{ props.contadorHijo}</p>
        </div>

    );
}

export default class Estado extends React.Component{
    /* Le definimos el estado en su metodo contructor de la clase */
    constructor(props) {
        super(props);
        this.state = {
            contador: 0,
        };

        /* Actualizamos su estado cada segundo con su metodo setState */
        
        /*Descomentar Interval para ver cambiso de estado */

        /*setInterval(() => {
            this.setState({
                contador: this.state.contador + 1
            });
        }, 1000);*/
    }

    
    


    render() {
        return (
            <div>
                <h3>Compontente de clases con estado</h3>
                <p>{this.state.contador}</p>

                <EstadoAHijo contadorHijo={ this.state.contador}/>
            </div>
        );
    }

}