import React, { useState, useEffect } from 'react';

function Pokemon(props) {
    return (
        <figure>
            <img src={props.avatar} alt={props.name} />
            <figcaption>{props.name}</figcaption>
        </figure>
    );
}

export default function AjaxHooks() {

    const [pokemons, setpokemons] = useState([]);

    //FETCH NORMAL
    /* useEffect(() => {
        let url = `https://pokeapi.co/api/v2/pokemon?limit=3&offset=100`;
        fetch(url)//pasamos la url
            .then((res) => res.json())//convertimos a json
            .then((json) => {//con la respuesta vas a hacer esto

                json.results.forEach(el => {//segunda peticion para obtener imagen
                    
                    fetch(el.url)
                        .then((res) => res.json())
                        .then((json) => {
                            let pokemon = {
                                id: json.id,
                                name: json.name,
                                avatar: json.sprites.front_default,
                            }

                            setpokemons((pokemons)=>[...pokemons, pokemon])

                        });
                });
            });
    }, []); */



    
    //FETCH CON FUNCIONES ASINCRONAS
    //No volver useEffect Asincrono, se crea una nueva funcion y es esta 
    //la que se hace asincrona


    useEffect(() => {
        const getPokemons = async (url) => {
            let res = await fetch(url),
                json = await res.json();
            
                json.results.forEach(async (el) => {//segunda peticion para obtener imagen
                    let res = await fetch(el.url),
                        json = await res.json();
                    
                            let pokemon = {
                                id: json.id,
                                name: json.name,
                                avatar: json.sprites.front_default,
                            }

                            setpokemons((pokemons)=>[...pokemons, pokemon])

                        });
            
        }

        getPokemons(`https://pokeapi.co/api/v2/pokemon?limit=3&offset=100`);
        
    }, []);

    return (
        <>
            <h2>Peticiones Asíncronas con Hooks</h2>
            {pokemons.map((el) => (
                <Pokemon key={el.id} name={el.name} avatar={el.avatar}></Pokemon>
            ))}
        </>
    );
}