import styled,{css, keyframes, ThemeProvider, GlobalStyleComponent, createGlobalStyle} from 'styled-components';

export default function StyledComponets() {

    let mainColor = "#00bbff",
        alphaColor = "#00bbff95";


    /* Podemos usar estilos globales, estos nos pueden ayudar para el reseteo
    y se recomienda hacer en la parte mas alta de la jerarquia de carpetas
    para que afecte a todos los componentes de la App, aqui podemos afectas a todos
    los elementos, ojo necesitamos importar a globasState */
    const GlobalStyle = createGlobalStyle`
        body{
            /* Podemos afectas al body */
        }
        main{
            /* Podemos afectar al main y a todo */
        }

        h2{
            padding:2rem;
            background-color:#fff;
            color:#61dafb;
            text-transform:uppercase;
        }
    
    `;

    //Podemos usar animaciones, necesitas importar keyframes en styledComponts
    //La animacion la tenemos que llamar en el componente donde la queremos
    //En este caso en MyH3 abajo de border-radius.
    const fadein = keyframes`
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
    `;
    
    //Definir h3 desde styledComponents
    const MyH3 = styled.h3`
    text-align:center;
    margin: .5rem 3rem;
    padding:1rem;
    background-color: ${mainColor};
    transition: all .2s ease-in;
    cursor:pointer;
    border-radius:20px;
    //Animación
    animation: ${fadein} 2s ease-in;


    /* Si nosotros al elemento le pasamos props, se las
    va a asignar si no, no le asigna nada, es el caso de
    esto, uno recibe un h3 con color y el tro no */
    //Podemos hacer hasta renderizado condicional, si
    //no viene props.color aplica el sig color:
    color: ${(props) => props.color|| "#e7e7e7"};
    



    /* Para aplicar estilos de boton, hacemos las validacion y llamamos al
    css que importamos que nos permite aplicar estilos dentro de estilos */
    ${(props) => props.isBoton && css`
    
    margin: 1rem auto;
    max-width:40%;
    border-radius:.25rem;
    cursor:pointer;
    font-size:13px;
    `}

    &:hover{
        background-color: ${alphaColor};

    }
    `;

    //Si queremos aplicar modo dia y noche a un contexto
    //Necesitamos importar ThemeProvider,, y las dos var con
    //los estilos de dia y noche como propiedades de un objeto
    const dia = {
        color: "#222",
        bgColor: "#ddd",
    };

    const noche = {
        color: "#ddd",
        bgColor: "#222",
    };


    const Mybox = styled.div`
    padding: 1rem;
    margin:1rem;
    color:${({ theme }) => theme.color};
    background-color:${({ theme }) => theme.bgColor};
    `;

    /* Podemos hacer herencia de clases css, si queremos una div
    pero con border redondeados, heredamos los elementos ya hechos
    de mibox y le añadimos los bordes que queremos. */
    const MyBoxRadios = styled(Mybox)`
    border-radius:1rem;
    font-size:13px;
    `;



    return (
        <>
            {/* Este component aunque no esta en la raiz va a afectar toda la app, haci que
            ten cuidado */}
            <GlobalStyle></GlobalStyle>


            <h2>Componentes Estilizadoss</h2>
            <MyH3>Soy un h3 con Styled Components</MyH3>

            
            {/* Igual podemos pasar var personalizadas a mis elementos
            es el caso de este h3 que recibe un color */}
            <MyH3 color="#000">H3 con color pasado en props</MyH3>
        
            {/* Podemos establecer styles a elements dependiendo si traen
            ciertas propiedades, para esto necesitas importar {css} de styles */}

            <MyH3 isBoton={true}>Soy un h3 con styles de botón</MyH3>
        
        {/* Para el tema oscuro y dia, se llama A teamProvider y en team
        se pasa el objeto con las propiedades de las contantes ya definidas
        y dentro los o el elemento, Ya que  ThemeProvider es solo un contenedor*/}
            <ThemeProvider theme={dia}>
                <Mybox>Es dia, a levantarse</Mybox>
            </ThemeProvider>
            
            <ThemeProvider theme={noche}>
                <Mybox>Es de noche, a mimir</Mybox>
                <MyBoxRadios>Soy una caja de noche con elements heredados de la box de arriba</MyBoxRadios>
            </ThemeProvider>
        </>
    );
}

