import React, { Component } from 'react';

function Pokemon(props) {
    return (
        <figure>
            <img src={props.avatar} alt={props.name} />
            <figcaption>{props.name}</figcaption>
            <p>{props.description}</p>
        </figure>
    )
}

export default class AjaxApi extends Component{
    state = {
        pokemones: [],
    };


    //Peticiones a API
    componentDidMount() {
        let url = `https://pokeapi.co/api/v2/pokemon?limit=3&offset=100`;
        fetch(url)//pasamos la url
            .then((res) => res.json())//convertimos a json
            .then((json) => {//con la respuesta vas a hacer esto

                json.results.forEach(el => {//segunda peticion para obtener imagen
                    
                    fetch(el.url)
                        .then((res) => res.json())
                        .then((json) => {
                            let pokemon = {
                                id: json.id,
                                name: json.name,
                                avatar: json.sprites.front_default,
                            }

                            let pokemones = [...this.state.pokemones, pokemon];

                            this.setState({ pokemones });
                        });
                });
            });
    }



    render() {
        return (
            <>
                <h2>Peticiones Asíncronas en Componentes de Clase</h2>
                {this.state.pokemones.map((el) => (
                    <Pokemon key={el.id} name={el.name} avatar={el.avatar}></Pokemon>
                ))}
            </>
        )
    }
}